import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import AuthService from "../../services/auth.service";
import { useNavigate } from "react-router-dom";

export default function Home() {
  const navigate = useNavigate();
  useEffect(() => {
    AuthService.getTestAPI(); //render
    if (localStorage.getItem("user")) navigate("/profile");
  }, []);

  return (
    <main>
      <div className="container py-4">
        <div className="mb-4">
          <div className="h-100 p-5 text-white bg-dark rounded-3">
            <h1 className="display-5 fw-bold"></h1>
            <p className="fs-4">
               React.js Node.jsMongoDB
            </p>
            <p>
              <a
                className="link-light link-underline-opacity-0 link-underline-opacity-75-hover"
                target="blank"
                href="https://github.com/KuKuO8112/Project_MERN_Client"
              >
                Github client code
              </a>
            </p>
            <p>
              <a
                className="link-light link-underline-opacity-0 link-underline-opacity-75-hover"
                target="blank"
                href="https://github.com/KuKuO8112/Project_MERN_Server"
              >
                Github server code
              </a>
            </p>
          </div>
        </div>

        <div className="row align-items-md-stretch">
          <div className="col-md-6">
            <div className="h-100 p-5 bg-light border rounded-3">
              <h2></h2>
              <Link
                style={{ marginTop: "2rem" }}
                className="btn btn-outline-secondary"
                type="button"
                to={"/Login"}
              >
                
              </Link>
            </div>
          </div>
          <div className="col-md-6 mt-3">
            <div className="h-100 p-5 bg-light border rounded-3">
              <h2>
                ()
              </h2>
              <Link
                style={{ marginTop: "2rem" }}
                className="btn btn-outline-secondary"
                type="button"
                to={"/register"}
              >
                
              </Link>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
}
