import React from "react";

export default function InTitle({ handleTitle, title }) {
  return (
    <select
      className="form-select form-select-lg fs-4"
      onChange={handleTitle}
      name="title"
      value={title ? title : ""}
    >
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </select>
  );
}
